import React, {useState} from 'react'
import styled from 'styled-components'
import './firebase.ts'
import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'
import useUser from './useUser'

export default function App() {
  const {user, data, save, loading} = useUser()
  const [input, setInput] = useState('')

  if (loading) return <Main></Main>

  if (user)
    return (
      <Main>
        <User>{user.displayName}</User>
        <Form onSubmit={onAdd}>
          <Input value={input} onChange={(e) => setInput(e.target.value)} />
          <Links>
            {data?.links?.map((link, index) => (
              <Link href={link} key={index}>
                {link}
              </Link>
            ))}
          </Links>
        </Form>
      </Main>
    )

  return (
    <Main>
      <button onClick={signin}>Login</button>
    </Main>
  )

  function onAdd(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault()
    save({links: [input, ...(data?.links ?? [])]})
    setInput('')
  }

  async function signin() {
    await signinWithGoogle()
  }
}

const Links = styled.div`
  list-style: none;
  padding: 0;
  margin-top: 20px;
`

const Link = styled.a.attrs({target: '_blank'})`
  display: block;
  padding: 20px;
  border: 1px solid #23395d;
  background-color: #1c2f4a;
  border-radius: 10px;
  margin-top: 10px;

  color: #d5d0d0;
`

const User = styled.div`
  border: 1px solid #23395d;
  border-radius: 5px;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 10px;
`

const Input = styled.input.attrs({placeholder: 'Add a link...'})`
  padding: 10px 20px;
  width: 100%;
  font-size: 18px;
  border: 1px solid #ccc;
  border-radius: 8px;
  outline: none;
  opacity: 0.8;
`

const Form = styled.form`
  max-width: 600px;
  width: 100%;
`

const Main = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100%;

  background-color: #162238;
  color: #d5d0d0;
`

const auth = firebase.auth()
const googleProvider = new firebase.auth.GoogleAuthProvider()

async function signinWithGoogle() {
  await auth.signInWithPopup(googleProvider)
}
